window.dom = {
  //create:function(){}
  //åˆ›å»ºèŠ‚ç‚¹
  create(string) {
    const container = document.createElement("template");
    container.innerHTML = string.trim(); //åŽ»æŽ‰ç©ºæ ¼
    return container.content.firstChild;
  },
  //æ–°å¢žå¼Ÿå¼Ÿã€‚åœ¨nodeèŠ‚ç‚¹åŽé¢æ–°å¢žä¸€ä¸ªèŠ‚ç‚¹node2.
  after(node, node2) {
    node.parentNode.insertBefore(node2, node.nextSibling); //æŠŠnode2æ’å…¥åˆ°nodeçš„ä¸‹ä¸€ä¸ªèŠ‚ç‚¹çš„å‰é¢
  },
  //æ–°å¢žå“¥å“¥ã€‚åœ¨nodeèŠ‚ç‚¹å‰é¢æ–°å¢žèŠ‚ç‚¹node2
  before(node, node2) {
    node.parentNode.insertBefore(node2, node);
  },
  //æ–°å¢žå„¿å­ã€‚
  append(parent, node) {
    parent.appendChild(node);
  },
  //æ–°å¢žçˆ¸çˆ¸ã€‚
  wrap(node, parent) {
    dom.before(node, parent); //å…ˆæŠŠparentèŠ‚ç‚¹æ’å…¥åˆ°nodeèŠ‚ç‚¹å‰é¢ï¼ŒæŠŠnodeèŠ‚ç‚¹ä»Žæ ‘ä¸­ç§»å‡ºæ¥ã€‚
    dom.append(parent, node); //å†æŠŠnodeèŠ‚ç‚¹æ’å…¥åˆ°parentèŠ‚ç‚¹åŽé¢ã€‚
  },
  //åˆ é™¤èŠ‚ç‚¹
  remove(node) {
    node.parentNode.removeChild(node);
    return node; //åˆ é™¤çš„è¿™ä¸ªèŠ‚ç‚¹å¯èƒ½ä¼šç”¨åˆ°
  },
  //åˆ é™¤è¿™ä¸ªèŠ‚ç‚¹çš„æ‰€æœ‰å„¿å­ï¼Œä½†æ˜¯ä¸åˆ é™¤è‡ªå·±
  empty(node) {
    //const childNodes = node.childNodes;
    //const { childNodes } = node;
    const siblings = [];
    let x = node.firstChild;
    while (x) {
      siblings.push(dom.remove(node.firstChild));
      x = node.firstChild;
    }
    return siblings;
  },
  //ç»™å…ƒç´ åŠ å±žæ€§ã€‚è¯»å†™å±žæ€§
  attr(node, name, value) {
    //é‡è½½ã€‚æ ¹æ®å‚æ•°ä¸ªæ•°å†™ä¸åŒä»£ç å°±æ˜¯é‡è½½ã€‚
    if (arguments.length === 3) {
      node.setAttribute(name, value);
    } else if (arguments.length === 2) {
      return node.getAttribute(name);
    }
  },
  //è¯»å†™æ–‡æœ¬å†…å®¹
  text(node, string) {
    //é€‚é…
    if (arguments.length === 2) {
      //å¦‚æžœé•¿åº¦ä¸º2ï¼Œç”¨äºŽè®¾ç½®æ–‡æœ¬å†…å®¹
      if ("innerText" in node) {
        node.innerText = string; //ie
      } else {
        node.textContent = string; //Firefox,Chrome
      }
    } else if (arguments.length === 1) {
      //å¦‚æžœé•¿åº¦ä¸º1ï¼Œç”¨äºŽè¯»å–æ–‡æœ¬å†…å®¹
      if ("innerText" in node) {
        return node.innerText;
      } else {
        return node.textContent;
      }
    }
  },
  //è¯»å†™HTMLå†…å®¹
  html(node, string) {
    if (arguments.length === 2) {
      node.innerHTML = string;
    } else if (arguments.length === 1) {
      return node.innerHTML;
    }
  },
  //æ·»åŠ æ ·å¼
  style(node, name, value) {
    if (arguments.length === 3) {
      //dom.style(div,'color','red')
      node.style[name] = value;
    } else if (arguments.length === 2) {
      if (typeof name === "string") {
        //dom.style(div,'color')
        return node.style[name];
      } else if (name instanceof Object) {
        //dom.style(div,{color:'red'})
        let object = name;
        for (let key in object) {
          //key: node.style.border
          node.style[key] = object[key];
        }
      }
    }
  },
  class: {
    add(node, className) {
      node.classList.add(className);
    },
    remove(node, className) {
      node.classList.remove(className);
    },
    has(node, className) {
      return node.classList.contains(className);
    },
  },
  on(node, eventName, fn) {
    node.addEventListener(eventName, fn);
  },
  off(node, eventName, fn) {
    node.removeEventListener(eventName, fn);
  },
  find(selector, scope) {
    return (scope || document).querySelectorAll(selector);
  },
  parent(node) {
    return node.parentNode;
  },
  children(node) {
    return node.children;
  },
  siblings(node) {
    return Array.from(node.parentNode.children).filter((n) => n !== node);
  },
  next(node) {
    let x = node.nextSibling;
    while (x && x.nodeType === 3) {
      x = x.nextSibling;
    }
    return x;
  },
  previous(node) {
    let x = node.previousSibling;
    while (x && x.nodeType === 3) {
      x = x.previousSibling;
    }
    return x;
  },
  each(nodeList, fn) {
    for (let i = 0; i < nodeList.length; i++) {
      fn.call(null, nodeList[i]);
    }
  },
  index(node) {
    let list = dom.children(node.parentNode);
    let i;
    for (i = 0; i < list.length; i++) {
      if (list[i] === node) {
        break;
      }
    }
    return i;
  },
};
